body,
h1 {
  margin: 0;
  padding: 0;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Helvetica,
    Arial, sans-serif, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol';
}

/* Styles for smaller screens */
@media screen and (max-width: 600px) {
  .header-title {
    font-size: 2em;
  }
  
  .header-subtitle {
    font-size: 0.8em;
  }
  
  .header-desc {
    font-size: 0.9em;
  }

  .header {
    flex-direction: column;
    text-align: center;
  }

  .header-image {
    margin-left: 0; /* Reset margin to avoid overlap */
    margin-bottom: 20px; /* Add margin below image */
  }

  .header-title::after {
    content: ' | ';
    padding: 0 0.5rem;
    color: #000000;
    font-family: IM Fell English; 
  }
}

.header {
  display: flex;
  align-items: center;
  min-height: 60px;
  padding: 0.5rem 1rem;
  background-image: linear-gradient(284deg, #DDCFB6, #DDCFB6);
  /* background-image: "blankpaper.jpg"; */
  color: #000;
  margin-bottom: 1rem;
}

/* .header-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
} */

.header-image {
  width: 65px; /* Adjust the width and height as needed */
  height: 55px;
  position: absolute;
  top: 10px; /* Adjust the top and right values for positioning */
  right: 10px;
}

.header a {
  color: #000;
  text-decoration: none;
}

.header-title {
  font-size: 1.5rem;
  font-weight: normal;
  font-family: IM Fell English; 
  color: #000000;
}

.header-title::after {
  content: ' | ';
  padding: 0 0.5rem;
  color: #000000;
  font-family: IM Fell English; 
}

.header-subtitle {
  font-size: 1.2rem;
  font-family: IM Fell English; 
  color: #000000;
}

.header-desc {
  font-size: 1.0rem;
  font-family: IM Fell English; 
  color: #000000;
  padding: 0.5rem 1rem;
}

