/* Style the container for the extra-info box */
.extra-info-container {
  /* Adjust width and positioning if needed */
  max-width: 60%; /* Example maximum width */
  margin: 20px auto; /* Example margin for centering */
}

/* Style the extra-info box */
.extra-info {
  /* Style the box with appropriate width, height, background, etc. */
  padding: 10px;
  background-color: #ffe4c4; /* Light yellow-brown shading */
  border: 1px solid black; /* Thin black outline */
  border-radius: 5px; /* Optional: Rounded corners */
  position: relative; /* Make the container position-relative for positioning the dismiss button */
}


/* Style the acknowledgment dismiss button */
.dismiss-button {
  background: none;
  border: none;
  cursor: pointer;
  position: absolute;
  top: 5px;
  right: 5px;
  color: #333; /* Adjust icon color as needed */
}

/* Hover effect for the dismiss button */
.dismiss-button:hover {
  color: #ff0000; /* Change color on hover if desired */
}


.acknowledgment-box {
  /* Adjust width and positioning if needed */
  max-width: 75%; /* Example maximum width */
  margin: 20px auto; /* Example margin for centering */
}

/* Style the extra-info box */
.header-acknowledgment {
  /* Style the box with appropriate width, height, background, etc. */
  padding: 10px;
  background-color: #ffe4c4; /* Light yellow-brown shading */
  border: 1px solid black; /* Thin black outline */
  border-radius: 5px; /* Optional: Rounded corners */
  position: relative; /* Make the container position-relative for positioning the dismiss button */
}




