/* Container */
/* Clear refinements div should span same width as container */
.clear-refinements{
  display: flex;
  flex-direction: column;
  align-items: center;
}


.container {
  max-width: 1500px;
  margin: 0 auto;
  padding: 1rem;
}

/* Search */
.search-panel {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

.search-panel__filters {
  display: flex;
  gap: 1rem;
  flex-wrap: wrap;
}

.search-panel__pagination {
  margin: 0 auto 1rem;
}

/* Custom category dropdown */

.my-CategoryDropdown .ais-HierarchicalMenu {
  height: 185px;
  overflow: auto;
}

/* Custom brand dropdown */
.my-SearchableDropdown .ais-RefinementList-list {
  /* width: 20rem; */
  display: flex;
  flex-wrap: wrap;
}

.my-SearchableDropdown .ais-RefinementList-item {
  margin: 0.2rem;
  padding: 0.1rem 0.5rem;
  border-radius: 2em;
  background: #ccc;
}

.my-SearchableDropdown .ais-RefinementList-item .ais-RefinementList-checkbox {
  display: none;
}

.my-SearchableDropdown .ais-RefinementList-item.ais-RefinementList-item--selected {
  background: #333;
  color: #fff;
}

.my-SearchableDropdown .ais-RefinementList-label mark {
  background: none;
  text-decoration: underline;
  font-weight: bold;
}
